@import "../../global.scss";

.topbar {
    width: 100%;
    height: 70px;
    background-color: $secondColor;
    color: $mainColor;
    position : fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left {
            display: flex;
            align-items: center;

            .logo {
                font-size: 2vw;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                margin-right: 20px;
                margin-left: 20px;
            }

            .itemContainer {
                display: flex;
                align-items: center;
                margin-left: 30px;
                margin-right: 30px;

                a {
                    text-decoration: none;
                    color:inherit;
                }
                .icon{
                    font-size: 18px;
                    margin-right: 5px;
                }

                span {
                    font-size: 15px;
                    font-weight: 500;
                }
            }
        }
        .right {

                .hamburger {
                    width: 32px;
                    height: 25px;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    cursor: pointer;

                    span {
                        width: 100%;
                        height: 3px;
                        background-color: $mainColor;
                        transform-origin: left;
                        transition: all 2s ease;
                    }
                }
                

            }
    }

    // and if there is an active attribute use this css content
    &.active {
        background-color: $mainColor;
        color: #36454F;

        .hamburger {
            span {
                &:first-child {
                    background-color: #36454F;
                    transform: rotate(45deg);
                    
                }
                &:nth-child(2) {
                    opacity: 0;
                }
                &:last-child {
                    background-color: #36454F;
                    transform: rotate(-45deg);
                }
            }
        }
    }

}